import React from "react";
import "./Feedback.css";
import { v4 as uuidv4 } from "uuid";

const Feedback = ({options, onLeaveFeedback }) => {
  return (
    <>
    {options.map(option => (
      <button className="btn-set"
      type='button'
      key={uuidv4()}
      name={option}
            onClick={(e) => onLeaveFeedback(e)}>
          {option}
      </button>
      ))}
      </>
  );
};

export default Feedback;
